<template>
  <div class="header">
    <ul class="header-button-left">
      <li v-if="showTab != 0" @click="showTab = 0">Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="showTab === 1" @click="showTab = 2">Next</li>
      <li v-if="showTab === 2" @click="publish">Next</li>
    </ul>
    <img src="./assets/logo.png" class="logo" />
    <!-- <h4> dkssud {{$store.state.name}}</h4>
      <button @click="$store.commit('changeName')">변경</button> -->
    <!-- <p>{{ $store.state.more }}</p>
    <button @click="$store.dispatch('moreData')">더보기버튼!!</button> -->
  </div>

  <Container
    :data="instaData"
    :showTab="showTab"
    :file="url"
    :newFilter="newFilter"
    @write="text = $event"
  />
  <button v-if="showTab === 0" @click="more">더보기</button>
  <!-- <p>{{ now2 }} ,{{ clickCnt }}</p>
  <button @click="clickCnt++">클릭</button> -->
  <p>{{ name }}</p>
  <p>{{ getName }}</p>

  <div class="footer">
    <ul class="footer-button-plus">
      <input
        @change="upload"
        multiple
        accept="image/*"
        type="file"
        id="file"
        class="inputfile"
      />
      <label for="file" class="input-plus">+</label>
    </ul>
  </div>

  <!-- <div v-show="showTab === 0">내용0</div>
  <div v-show="showTab === 1">내용1</div>
  <div v-show="showTab === 2">내용2</div> -->
  <button @click="showTab = 0">버튼0</button>
  <button @click="showTab = 1">버튼1</button>
  <button @click="showTab = 2">버튼2</button>
</template>

<script>
import Container from "./components/Container";
import sample from "./assets/data.js";
import axios from "axios";
import { mapState, mapMutations } from "vuex";

export default {
  name: "App",
  components: {
    Container,
  },
  data() {
    return {
      instaData: sample,
      cnt: 0,
      showTab: 0,
      url: "",
      text: "",
      newFilter: "",
      clickCnt: 0,
    };
  },
  computed: {
    // return 필수!
    // name() {
    //   return this.$store.state.name;
    // }, // 길다 .  그래서 mapState를 사용한다.
    ...mapState(["name", "age"]),
    ...mapState({ getName: "name" }),

    //computed vs. methods
    now2() {
      //처음 실행할 때 값을 간직함. > 컴퓨팅파워를 저장할 수 있음-자원절약-. 계산결과 저장용
      return new Date();
    },
  },
  methods: {
    ...mapMutations(["setMore"]),

    now() {
      // 함수를 사용할 때마다 실행.
      return new Date();
    },
    publish() {
      let newData = {
        name: "na",
        userImage: "https://placeimg.com/100/100/arch",
        postImage: this.url,
        likes: 0,
        date: new Date(),
        liked: false,
        content: this.text,
        filter: this.newFilter,
      };
      console.log(newData);
      this.instaData.unshift(newData);
      this.showTab = 0;
      // this.newFilter = "";
    },
    more() {
      axios
        .get(`https://codingapple1.github.io/vue/more${this.cnt}.json`)
        .then((result) => {
          //요청 성공시 실행할 코드
          // console.log(result.data);
          this.instaData.push(result.data);
          this.cnt++;
        })
        .catch //실패시.
        ();
    },
    // btn(num){
    //   this.showTab = num;
    // }
    upload(e) {
      let files = e.target.files;
      // console.log(file[0].type);
      this.url = URL.createObjectURL(files[0]);
      this.showTab = 1;
    },
  },
  mounted() {
    // console.log(this.instaData);
    this.emitter.on("fire", (a) => {
      console.log(a);
    });
    this.emitter.on("setFilter", (f) => {
      this.newFilter = f;
    });
  },
};
</script>

