<template>
  <div @click="setFilter(filter)" class="filter-item" :class="filter" :style="{ backgroundImage : `url(${file})`}">
    <slot></slot>
    <!-- <button @click="fire">버튼</button> -->
</div> 
</template>

<script>
export default {
    name:'filterbox',
    props:{
        file : [],
        filter : String,
    },
    methods: {
        fire(){
            this.emitter.emit('fire','데이터');
        },
        setFilter(f){
            this.emitter.emit('setFilter',f);
        }
    },
}
</script>

<style>
    .filter-item {
    width: 100px;
    height: 100px;
    margin: 10px 10px 10px auto;
    padding: 8px;
    display: inline-block;
    color : white;
    background-size: cover;
    background-position : center;
    }
</style>