import { createStore } from "vuex";
import axios from "axios";
const store = createStore({
  state() {
    return {
      name: "kim",
      age: 8,
      likes: [], //안씀 ㅎ~

      more: {},
    };
  },
  mutations: {
    setMore(state, data) {
      state.more = data;
    },
    changeName(state) {
      state.name = "park";
    },
    clickLike(state, payload) {
      //안씀 테스트만 해봄.
      console.log(state.likes);
      console.log(payload);
    },
  },
  actions: {
    //ajax요청
    moreData(context) {
      axios.get("https://codingapple1.github.io/vue/more0.json").then((a) => {
        console.log(a.data);
        context.commit("setMore", a.data);
      });
    },
  },
});

export default store;
